<!--
 * @Description: 修改当前登录账户的信息
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-07 17:23:01
 * @LastEditTime: 2019-09-27 16:03:41
 -->

<template>
  <div>
    <v-layout row justify-center >
      <v-dialog v-model="dialog" width="800px" :persistent="true" >
        <template v-slot:activator="{ on }">
          <!-- <v-btn color="primary" flat dark v-on="on" >修改账户信息</v-btn> -->
          <v-chip color="secondary" text-color="white" v-on="on" >
          <v-avatar>
            <v-icon>account_circle</v-icon>
          </v-avatar>
          {{ userData.user_name }}
        </v-chip>
        </template>
        <v-card>
          <v-card-title>
            <span class="headline">修改账号信息 </span>
          </v-card-title>
          <v-card-text>
            <form>
              <!-- 账号 -->
              <v-text-field
                v-model="userData.user_name"
                type="text"
                maxlength="16"
                name="input-10-1"
                label="账号"
                hint
                counter
                disabled
              ></v-text-field>
              <!-- 真实姓名 -->
              <v-text-field
                v-model="userData.true_name"
                type="text"
                name="input-10-2"
                maxlength="26"
                label="真实姓名"
                hint
                counter
              ></v-text-field>
              <!-- 层级 -->
              <v-text-field
                v-model="userData.role_name"
                type="text"
                name="input-10-2"
                maxlength="26"
                label="层级"
                hint
                counter
                disabled
              ></v-text-field>
              <!-- 邮箱地址 -->
              <v-text-field
                v-model="userData.email"
                type="text"
                name="input-10-2"
                maxlength="26"
                label="邮箱"
                hint
                counter
              ></v-text-field>
              <!-- 手机 -->
              <v-text-field
                v-model="userData.phone"
                type="text"
                name="input-10-2"
                maxlength="26"
                label="手机号码"
                hint
                counter
              ></v-text-field>
              <!-- 性别 -->
              <v-text-field
                v-model="gender"
                type="text"
                name="input-10-2"
                maxlength="26"
                label="性别"
                hint
                counter
                disabled
              ></v-text-field>
              <!-- 产品序列号 -->
              <v-text-field
                v-model="order_sn"
                type="text"
                name="input-10-2"
                maxlength="26"
                label="产品序列号"
                hint
                counter
                disabled
              ></v-text-field>
              <p></p>
              <p> 创建时间： {{ userData.create_time | time }}</p>
              <p>修改时间： {{ userData.update_time | time }}</p>
              <i>（ *修改账号信息需要重新登录 ）</i>
            </form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" flat="flat" @click="dialog = false">取消</v-btn>
            <v-btn color="info darken-1" @click="user_modify">修改</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>

  </div>
</template>

<script>
import Api from "../../../server/api.js";
import { truncate } from 'fs';
import { setTimeout } from 'timers';
export default {
  data() {
    return {
      // 用户基本信息操作框
      loader: false,
      userData: this.$cookie.getLocalStorage("root_path"),

      // 修改用户信息框
      dialog: false
    };
  },
  computed: {
    gender(){
      return  this.userData.gender==1?'男':'女';
    },
    order_sn(){
      return  this.userData.order_sn==''?'暂无数据':this.userData.order_sn;
    }
  },
  methods: {
      // 修改提交
    user_modify(){
       if( this.userData.true_name.length < 2 ){ this.$Dialog.getProductCheckResult_({text: `请输入正确的真实姓名`,timeout: 3000, snackbar: true }); return false; }
       if( this.userData.email.length < 5 ){ this.$Dialog.getProductCheckResult_({text: `请输入正确的邮箱地址`,timeout: 3000, snackbar: true }); return false; }
       if( this.userData.phone.length !== 11 ){ this.$Dialog.getProductCheckResult_({text: `请输入正确的手机号码`,timeout: 3000, snackbar: true }); return false; }
       this.$axios({
        method: "post",
        url: Api.URL.manages_edit_manage,
        data: {
          email: this.userData.email,
          phone: this.userData.phone,
          true_name: this.userData.true_name
        }
      })
        .then(response => {
          //请求成功返回的数据
          if (response.data.result === 0) {
            this.dialog = false;
            this.$Dialog.getProductCheckResult_({text: "登录账户信息已更新，请重新登录！",timeout: 3000, snackbar: true });
            setTimeout(()=>{
              this.$router.push({
                path: `/`
              });
            },2000)
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
            this.$Dialog.getProductCheckResult_({text: '😡'+error,timeout: 3000, snackbar: true });
        });
    }
  }
};
</script>

<style lang="less" scoped>
</style>
