                <v-list-tile-content>
                  <v-list-tile-title>{{ item.text }}</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
              <div v-for="(child, i) in item.children" :key="i">
                <v-list-tile v-if="child.state" @click="geBug" :ripple="true">
                  <v-list-tile-action></v-list-tile-action>
                  <v-list-tile-content>
                    <v-list-tile-title>
                      <router-link :to="child.router">
                        <v-icon class="icon__">{{ child.icon }}</v-icon>
                        &nbsp; {{ child.text }}
                      </router-link>
                    </v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
              </div>
            </v-list-group>
            <!-- 控制单个侧边选项 -->
            <v-list-tile v-else :key="item.text">
              <v-list-tile-action v-if=" item.state ">
                <v-icon>{{ item.icon }}</v-icon>
              </v-list-tile-action>
              <v-list-tile-content v-if=" item.state ">
                <v-list-tile-title>
                  <router-link :to="item.router">{{ item.text }}</router-link>
