            solo-inverted
            @change="router"
            v-model="router_id"
            :items="router_list"
            label="快捷选择"
            solo
          ></v-select>
        </v-flex>
        <v-spacer></v-spacer>
        <!-- <v-chip color="secondary" text-color="white" @click="menu = true" >
          <v-avatar>
            <v-icon>account_circle</v-icon>
          </v-avatar>
          {{ root_path_data.user_name }}
        </v-chip>-->
        <Module />
        <v-btn icon large>
          <v-menu v-model="menu" :close-on-content-click="false" :nudge-width="200" offset-x>
            <template v-slot:activator="{ on }">
              <v-icon style="font-size: 32px;" dark v-on="on">account_box</v-icon>
            </template>
            <v-card>
              <v-list>
                <v-list-tile avatar>
                  <v-list-tile-avatar>
                    <img src="../../assets/account-circle-outline.png" alt="John" />
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title>{{ root_path_data.user_name }}</v-list-tile-title>
                    <v-list-tile-sub-title>{{ root_path_data.role_name }}</v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>
              </v-list>
              <v-divider></v-divider>
              <v-list>
                <v-list-tile>
                  <v-btn to="/modifyPass" color="primary" flat @click="menu = false">修改登录密码</v-btn>
                </v-list-tile>
                <!-- <v-list-tile>
                  <Module />
                </v-list-tile>-->
              </v-list>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn flat @click="menu = false">取消</v-btn>
                <v-layout row justify-center>
                  <v-dialog v-model="dialog_logout" persistent max-width="290">
                    <template v-slot:activator="{ on }">
                      <v-btn color="warning" flat v-on="on">退出登录</v-btn>
                    </template>
                    <v-card>
                      <v-card-title class="headline">要退出本次登录吗?</v-card-title>
                      <v-card-text>
                        将前往到登录页面！（请勿在公共计算机上使用记住密码功能）
                        <div class="text-xs-center">
                          <v-btn
                            flat
                            :disabled="del_dialog"
                            :loading="del_dialog"
                            class="white--text"
                            color="green darken-1"
                            @click="del_cookies"
                          >退出并清除Cookies数据</v-btn>
                          <v-dialog v-model="del_dialog" hide-overlay persistent width="300">
                            <v-card color="primary" dark>
                              <v-card-text>
                                正在清除缓存数据..
                                <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                              </v-card-text>
                            </v-card>
                          </v-dialog>
                        </div>
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="green darken-1" flat @click="dialog_logout = false">取消</v-btn>
                        <v-btn color="primary darken-1" flat @click="logoutIng">退出登陆</v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-layout>
              </v-card-actions>
            </v-card>
          </v-menu>
        </v-btn>
      </v-toolbar>
      <!-- 路由 <keep-alive></keep-alive> -->
      <router-view></router-view>
    </v-app>
  </div>
</template>
<script>
import Module from "./components/module.vue";
import Cookies from "../../server/cookies.js";
import Api from "../../server/api.js";

export default {
  components: {
    Module
  },
  data: () => ({
    // 登录者信息
    root_path: {},
    // 侧边控制
    drawer__: true,
    // 搜索
    router_id: "",
    router_list: [],
    // 退出登录确认框
    del_dialog: false,
    dialog_logout: false,
    // 个人信息
    menu: false,
    dialog: false,
    drawer: null,
    // 侧边栏数据
    items: [
      { icon: "local_florist", text: "首页", state: false, router: "/home" },
      {
        icon: "expand_less",
        "icon-alt": "chevron_right",
        text: "用户管理",
        model: true,
        state: false,
        children: [
          {
            icon: "group",
            text: "用户列表",
            state: false,
            router: "/userList",
            url: "/api/v1/user/list"
          },
          {
            icon: "recent_actors",
            text: "医生列表",
            state: false,
            router: "/doctorList",
            url: "/api/v1/doctor/list"
          },
          {
            icon: "person_add_disabled",
            text: "待审核列表",
            state: false,
            router: "/doctorCollate",
            url: "/api/v1/doctor/collate"
          }
        ]
      },
      {
        icon: "expand_less",
        "icon-alt": "chevron_right",
        text: "订单管理",
        model: false,
        state: false,
        children: [
          {
            icon: "credit_card",
            text: "充值订单",
            state: false,
            router: "/order",
            url: "/api/v1/user/list"
          },
          {
            icon: "monetization_on",
            text: "提现订单",
            state: false,
            router: "/withdrawalOrder",
            url: "/api/v1/order/transfer_list"
          },
          {
            icon: "money_off",
            text: "退款订单",
            state: false,
            router: "/refundOrder",
            url: "/api/v1/user/list"
          },
          {
            icon: "report",
            text: "异常订单日志",
            state: false,
            router: "/oddOrder",
            url: "/api/v1/manages/query_fatal_log"
          }
        ]
      },
      {
        icon: "expand_less",
        "icon-alt": "chevron_right",
        text: "代理管理",
        model: false,
        state: false,
        children: [
          {
            icon: "contacts",
            text: "代理列表",
            state: false,
            router: "/agentList",
            url: "/api/v1/manages/query_manage"
          }
        ]
      },
      {
        icon: "expand_less",
        "icon-alt": "chevron_right",
        text: "设备管理",
        model: false,
        state: false,
        children: [
          {
            icon: "blur_on",
            text: "上盖列表",
            state: false,
            router: "/deviceList_",
            url: "/api/v1/device/query_sn"
          },
          {
            icon: "blur_circular",
            text: "底座列表",
            state: false,
            router: "/deviceList",
            url: "/api/v1/device/query_sn"
          },
          {
            icon: "cloud_upload",
            text: "批量导入",
            state: false,
            router: "/batchImport",
            url: "/api/v1/device/update_sn"
          }
        ]
      },
      {
        icon: "expand_less",
        text: "子账号管理",
        "icon-alt": "chevron_right",
        model: false,
        state: false,
        children: [
          {
            icon: "device_hub",
            text: "子账号列表",
            state: false,
            router: "/account",
            url: "/api/v1/manages/query_manage"
          },
          {
            icon: "transfer_within_a_station",
            text: "角色列表",
            state: false,
            router: "/roleList",
            url: "/api/v1/manages/query_role"
          }
        ]
      },
      {
        icon: "expand_less",
        text: "热更算法",
        "icon-alt": "chevron_right",
        model: false,
        state: false,
        children: [
          {
            icon: "line_style",
            text: "ED算法",
            state: false,
            router: "/algorithm",
            url: "/api/v1/manages/upload_algo_file"
          }
        ]
      },
      {
        icon: "receipt",
        text: "操作日志",
        state: false,
        router: "/operationLog"
      },
      {
        icon: "speaker_notes",
        text: "APP用户意见",
        state: false,
        router: "/opinion",
        url: "/api/v1/user/feedback"
      },
      {
        icon: "textsms",
        text: "官网用户留言",
        state: false,
        router: "/websiteMessage",
        url: "/api/v1/user/feedback"
      },
      {
        icon: "toys",
        text: "开发测试组件",
        state: false,
        router: "/test",
        url: "/api/v1/user/feedback"
      }
    ],
    transitionName: ""
  }),
  watch: {
    dialog(val) {
      if (!val) return;
      setTimeout(() => (this.del_dialog = false), 500);
    }
  },
  mounted: function() {
    this.power();
  },
  computed: {
    root_path_data() {
      return this.$cookie.getLocalStorage("root_path");
    }
  },
  methods: {
    /** 规避esLint */
    geBug(){},
    // 权限处理
    power() {
      let root_path = this.$cookie.getLocalStorage("root_path").root_path;
      // 生成二级菜单栏的状态
      root_path.forEach(root_item => {
        this.items.forEach(router_item => {
          if (router_item.children !== undefined) {
            router_item.children.forEach(children_item => {
              if (root_item.url == children_item.url) {
                children_item.state = true;
              }
            });
          }
        });
      });
      // 根据二级生成一级菜单栏
      this.items.forEach(router_item => {
        if (router_item.children !== undefined) {
          router_item.children.forEach(children_item => {
            if (children_item.state == true) {
              router_item.state = true;
            }
          });
        }
      });
      // 无二级菜单的一级菜单栏
      this.items.forEach(router_item => {
        if (router_item.url !== undefined) {
          root_path.forEach(root_item => {
            if (router_item.url == root_item.url) {
              router_item.state = true;
            }
          });
        }
      });
      // 首页,操作日志，用户反馈移除权限系统
      this.items.forEach(router_item => {
        if (router_item.text == "首页" || router_item.text == "操作日志") {
          router_item.state = true;
        }
      });
      // 快捷操作列表
      this.init();
    },
    init() {
      let list = [];
      this.items.forEach(item => {
        if (item.children !== undefined) {
          list.push(...item.children);
        }
      });
      list.forEach(item => {
        if (item.state) {
          this.router_list.push(item.text);
        }
      });
    },
    rootPath() {},
    // 清除cookies并退出
    del_cookies() {
      this.del_dialog = true;
      Cookies.delCookie("user");
      this.dialog_logout = false;
      setTimeout(() => {
        this.logoutIng();
        this.del_dialog = false;
      }, 900);
    },
    // 头部筛选框
    router() {
      this.drawer__ = false;
      let list = [];
      this.items.forEach(item => {
        if (item.children !== undefined) {
          list.push(...item.children);
        }
      });
      list.forEach(item => {
        if (this.router_id == item.text) {
          this.$router.push({
            path: item.router
          });
          return false;
        }
      });
    },
    // 退出登录
    logoutIng() {
      this.$axios({
        method: "post",
        url: Api.URL.manages_logout,
        data: {}
      })
        .then(response => {
          if (response.data.result === 0) {
            this.$router.push({
              path: `/`
            });
            this.$Dialog.getProductCheckResult_({text: "已退出登录",timeout: 3000, snackbar: true });
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: '😡'+error,timeout: 3000, snackbar: true });
        });
    }
  }
};
</script>
